/* ScreenRecording.css */
.container {
    margin-top: 5px; /* Adjust as needed */
    padding-top: 150px;
    padding-bottom: 280px;
    color: #fff;
    
    text-align: center;
    background-image: url("C:\Users\ajoep\Desktop\record\demo\src\OIP.jpg");
  }
  
  .title {
    font-size: 2rem;
    margin-bottom: 20px;
  }
  
  .button-group {
    margin-top: 20px;
  }
  
  .start,
  .stop,
  .download {
    background-color: #7e57c2;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 15px 30px;
    font-size: 1.2rem;
    cursor: pointer;
    margin-right: 10px;
  }
  
  .stop {
    background-color: #512DA8;
  }
  
  .download {
    background-color: #4CAF50; /* Green color for download button */
  }
  
  .start:hover,
  .stop:hover,
  .download:hover {
    opacity: 0.8;
  }
  
  .start:disabled,
  .stop:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
  